<template>
  <div class="client-info-detail frame-container box align-default short">

    <!-- 补白 -->
    <div class="client-info-detail__padding"></div>

    <!-- 导航栏 -->
    <div class="client-info-detail__nav box plr-30 align-hor-bet">
      <div class="icon iconfont  iconreturn"
           @click="onBack">
      </div>
      <div class="title">
        {{merchantName}}
      </div>
      <div class="pass"></div>
    </div>

    <!-- 信息主体 -->
    <div class="client-info-detail__content box match-left-space">
      <div class="item">
        <div class="subtitle">
          <span class="star">*</span>微信费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.wxRate"/>%
        </div>
      </div>
      <div class="item">
        <div class="subtitle">
          <span class="star">*</span>支付宝费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.zfbRate"/>%
        </div>
      </div>
      <div class="item">
        <div class="subtitle">
          随行付支付宝费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.sxfZfbRate"/>%
        </div>
      </div>
      <div class="item">
        <div class="subtitle">
          随行付微信费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.sxfWxRate"/>%
        </div>
      </div>
      <div class="item">
        <div class="subtitle">
          富友微信费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.fyWxRate"/>%
        </div>
      </div>
      <div class="item">
        <div class="subtitle">
          富友支付宝费率
        </div>
        <div class="match-left-space align-right input-number">
          <input type="number" v-model="rate.fyZfbRate"/>%
        </div>
      </div>
      <div class="match-width box align-default">
        <div class="title">结算信息</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>账户类型
          </div>
          <div class="match-left-space align-right">
            <div
              class="input align-right"
              @click="callActionSheet(1)">
              <div>{{detail.accountType}}</div>
              <div class="icon iconfont iconenter ml-10"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>开户名称
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入开户名称" v-model="detail.accountHolder"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>开户银行城市
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入开户行所在地(县/市)" v-model="detail.bankCity"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>开户银行
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入开户银行" v-model="detail.bankName"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>开户支行
          </div>
          <div class="match-left-space align-right">
            <input placeholder="开户支行名称" v-model="detail.bankOutlet"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>银行账号
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入银行账号并仔细核对" v-model="detail.accountNumber"/>
          </div>
        </div>
      </div>

      <div class="match-width box align-default">
        <div class="title">组织机构信息</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>组织机构代码
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入内容" v-model="detail.organizationCode"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>组织机构有效期
          </div>
          <div class="match-left-space align-right">
            <div class="input align-default">
              <mu-date-input
                @change="onDateTimeChange(1, 1, $event)"
                v-model="detail.startOrganizationTime"
                label="请输入开始时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
              <mu-date-input
                @change="onDateTimeChange(1, 2, $event)"
                v-model="detail.endOrganizationTime"
                label="请输入结束时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>组织机构代码照片
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(1)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.organizationPhotoId"
                :src="detail.organizationPhotoId | loadImage"
                @click="previewImage(detail.organizationPhotoId)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(1)"></div>
            </div>
          </div>
        </div>

      </div>

      <div class="match-width box align-center">
        <div
          class="next box align-center"
          @click="onNext">
          提交
        </div>
      </div>

      <!-- action-sheet -->
      <mu-bottom-sheet :open.sync="open">
        <div class="action-sheet box align-default">

          <div class="title box align-hor-bet plr-30">
            <div
              @click="onActionSheetClose">
              返回
            </div>
            <div
              class="confirm"
              @click="onActionSheetConfirm">
              确定
            </div>
          </div>

          <!-- 证件类型 -->
          <div
            class="match-width"
            v-for="(item, index) in accountTypes"
            :key="index"
            v-if="status == 1">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', index==curAccount?'active':'']"
              @click="changeItem(index)">
              <div>{{item}}</div>
              <div
                v-if="index==curAccount"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>

          <!-- 补脚 -->
          <div class="action-sheet__padding"></div>

        </div>
      </mu-bottom-sheet>

      <!-- action-sheet -->
      <mu-bottom-sheet :open.sync="openMenu">
        <div class="action-sheet box align-default menu">

          <div
            class="item align-center"
            @click="onTakePhoto(1)">
            拍照
          </div>

          <div
            class="item align-center"
            @click="onTakePhoto(2)">
            从手机相册选择
          </div>

          <div
            class="item align-center cancel"
            @click="onActionMenuShow(2)">
            取消
          </div>

          <!-- 补脚 -->
          <div class="action-sheet__padding"></div>

        </div>
      </mu-bottom-sheet>
      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="fileCamera" capture="camera" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>
      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="file" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>
    </div>

    <!-- 图片预览 -->
    <div class="cover-x align-default" v-if="isImagePreview" @click="isImagePreview = false">
      <div class="match-left-space"></div>
      <div class="image-preview">
        <img class="match-parent" :src="imgUrl | previewLoadImage">
      </div>
      <div class="match-left-space"></div>
    </div>

  </div>
</template>

<script>
import { url } from '@/utils/src/request'
import { CLIENT_INFO_DETAIL, CLIENT_INFO_RECORD_NEXT } from '@/router/types'
import { clientInfoDetailIDLocal, clientInfoDetailLocal, clientInfoDetailMerchantNameLocal } from '@/storage'
import { clientInfoApi } from '@/api'

export default {
  data() {
    return {
      detail: {},
      rate: {},
      open: false,
      status: 0,
      scenes: ['线下', '公众号/小程序', '网站', 'APP'],
      curScene: 0,
      majors: ['企业', '个体工商户'],
      curMajor: 0,
      appStates: ['已上线', '未上线'],
      curAppState: 0,
      openMenu: false,
      photoTaker: 0,
      accountTypes: ['对公账号'],
      curAccount: 0,
      isImagePreview: false,
      imgUrl: '',
      merchantName: ''
    }
  },

  methods: {
    // 返回
    onBack () {
      // TODO
      this.$router.push({
        name: CLIENT_INFO_RECORD_NEXT
      })
    },
    // 唤醒action-sheet
    callActionSheet (status) {
      this.open = true
      this.status = +status
      // status
      // 1  账户类型
      if (+status === 1) {
        // TODO
        this.curAccount = 0
      }
    },
    // action-sheet取消
    onActionSheetClose () {
      this.open = false
    },
    // action-sheet确认
    onActionSheetConfirm () {
      this.onActionSheetClose()
      if (this.status === 1) {
        // 账户类型
        this.detail.accountType = this.accountTypes[this.curAccount]
      }
    },
    // 切换选项
    changeItem (index) {
      if (this.status === 1) {
        // 账户类型
        this.curAccount = +index
      }
    },
    // 控制actionSheetMenu显隐
    onActionMenuShow (status) {
      if (+status === 2) {
        this.openMenu = false
      } else if (+status === 1) {
        this.openMenu = true
      }
    },
    // 相机打开
    onTakePhoto (status) {
      if (+status === 1) {
        // 拍照
        this.$refs.fileCamera.click()
      } else if (+status === 2) {
        // 从相机打开
        this.$refs.file.click()
      }
      // console.log('相机', this.photoTaker, status)
      this.openMenu = false
    },
    // 获取图片
    callTakePhoto (index) {
      this.openMenu = true
      this.photoTaker = +index
      // index
      // 1  组织机构代码照片
    },
    // 下一步
    onNext () {
      let requiredData = ['wxRate', 'zfbRate', 'accountType', 'accountHolder', 'bankCity', 'bankName', 'bankOutlet', 'accountNumber', 'organizationCode', 'startOrganizationTime', 'endOrganizationTime', 'organizationPhotoId']
      let flag = true
      for (let i in this.detail) {
        if (this.detail.hasOwnProperty(i) && requiredData.indexOf(i) !== -1) {
          if (!this.detail[i]) {
            flag = false
            break
          }
        }
      }
      if (flag) {
        for (let i in this.detail) {
          if (typeof this.detail[i] === 'string') {
            this.detail[i] = this.detail[i].trim()
          }
        }
        this.detail.wxRate = this.rate.wxRate / 100
        this.detail.zfbRate = this.rate.zfbRate / 100
        this.detail.sxfZfbRate = this.rate.sxfZfbRate / 100
        this.detail.sxfWxRate = this.rate.sxfWxRate / 100
        this.detail.fyZfbRate = this.rate.fyZfbRate / 100
        this.detail.fyWxRate = this.rate.fyWxRate / 100
        this.detail.status = 2
        console.log('提交', this.detail)
        clientInfoDetailLocal.set(JSON.stringify(this.detail))
        // return false;
        // 跳转到详情
        clientInfoApi.submit(this.detail).then(res => {

          if (res.code === 200) {
            this.$toast.success('提交成功')
            
            this.$router.push({
              name: CLIENT_INFO_DETAIL,
              query:{
                id:this.detail.id,
                isDirect:0
              }
            })
          } else {
            this.$toast.error(res.msg)
          }
        }, (err) => {
          this.$toast.error(err.msg)
        })
      } else {
        this.$toast.error('有内容未填入')
      }
    },
    // 时间改变
    onDateTimeChange (status, index, e) {
      //    status          index
      // 1  组织机构有效期   开始时间
      // 2                  结束时间
      if (+status === 1) {
        let startTime = this.detail.startOrganizationTime
        let endTime = this.detail.endOrganizationTime
        if (startTime && endTime) {
          startTime = this.getTimestamp(startTime)
          endTime = this.getTimestamp(endTime)
          if (startTime >= endTime) {
            this.$toast.error('开始时间必须早于结束时间')
            this.detail.startOrganizationTime = ''
            this.detail.endOrganizationTime = ''
          } else {
            this.detail.startOrganizationTime = this.getTime(this.detail.startOrganizationTime)
            this.detail.endOrganizationTime = this.getTime(this.detail.endOrganizationTime)
          }
        }
      }
    },
    // 时间戳
    getTimestamp (str) {
      return str ? (new Date(Date.parse(str)).getTime()) : 0
    },
    // 获取年月日
    getTime (str) {
      if (str) {
        // let tmp = new Date(Date.parse(str)).toLocaleDateString()
        let date = new Date(Date.parse(str))
        let y = date.getFullYear()
        let m = date.getMonth() + 1
        if (m < 10) {
          m = '0' + m
        }
        let d = date.getDate()
        if (d < 10) {
          d = '0' + d
        }
        // tmp = tmp.split('/').join('-')
        let tmp = y + '-' + m + '-' + d
        return tmp
      }
      return ''
    },
    // 初始化详情数据
    initDetail () {
      this.detail = JSON.parse(clientInfoDetailLocal.get())

      this.rate.wxRate = Number(this.detail.wxRate) ? Number(this.detail.wxRate) * 100 : 0 // 微信费率
      this.rate.zfbRate = Number(this.detail.zfbRate) ? Number(this.detail.zfbRate) * 100 : 0// 支付宝费率
      this.rate.sxfZfbRate = Number(this.detail.sxfZfbRate) ? Number(this.detail.sxfZfbRate) * 100 : 0 // 随行付费率
      this.rate.sxfWxRate = Number(this.detail.sxfWxRate) ? Number(this.detail.sxfWxRate) * 100 : 0 // 富友费率
      this.rate.fyZfbRate = Number(this.detail.fyZfbRate) ? Number(this.detail.fyZfbRate) * 100 : 0
      this.rate.fyWxRate = Number(this.detail.fyWxRate) ? Number(this.detail.fyWxRate) * 100 : 0

      this.merchantName = JSON.parse(clientInfoDetailMerchantNameLocal.get())
      this.merchantName = this.merchantName.merchantName
    },
    // 文件改变
    onFileChange (e) {
      console.log('e', e)
      if (!e.target.files) {
        return
      }
      let file = e.target.files[0]
      // let file = this.$refs.file.files[0]
      if (file) {
        // console.log(e)
        clientInfoApi.uploadImage(file).then(res => {
          if (res.code === 200) {
            this.$toast.success('图片上传成功')
            let photoId = res.obj
            // 1  组织机构代码照片
            if (this.photoTaker === 1) {
              this.$set(this.detail, 'organizationPhotoId', photoId)
            }
          } else {
            this.$toast.error(res.msg)
          }
        }, (err) => {
          this.$toast.error(err.msg)
        })
      }
    },
    // 预览图片
    previewImage (id) {
      // console.log(id)
      if (id) {
        this.imgUrl = id
        this.isImagePreview = true
      }
    }
  },

  filters: {
    // 获取图片
    loadImage (id) {
      return id ? url + `/fms/upload/resource/thumbnail/${id}` : ''
    },
    previewLoadImage (id) {
      return id ? url + `/fms/upload/resource/${id}` : ''
    }
  },

  mounted () {
    this.id = JSON.parse(clientInfoDetailIDLocal.get())
    // console.log('id 3', this.id)
    if (this.id) {
      this.initDetail()
    } else {
      this.$toast.error('详情数据丢失')
    }
  }
}
</script>



